body {
  background-color: #26312F;
  color: #E0ECC6;
  font-family: "Goudy", "Work Sans", Helvetica, sans-serif;
  font-weight: 300;
  line-height: 1.2;
}

@font-face {
  font-family: "TwCen";
  src: local("TwCen"), url('./fonts/Tw-Cen-MT.ttf') format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "Goudy";
  src: local("Goudy"), url('./fonts/GoudyBookletter1911.ttf') format("truetype");
  font-weight: normal;
}

h1, h2, h3, h4, h5, h6 {
  color: #D7B7E1;
  font-family: "TwCen", "Work Sans", Helvetica, sans-serif;
}

p {
  letter-spacing: .7px;
}

.input-class::placeholder {
  color: black;
  opacity: 1;
}

.input-class:focus {
  outline: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#describe-button:hover {
  background-color: black;
}

#green {
  color: #666652;
}

#pink {
  color: #E89B9A;
}

#teal {
  color: #319686;
}

#blue {
  color: #6EB0CC;
}



#green:hover, #pink:hover, #blue:hover, #teal:hover {
  color: white;
}